<template>
    <v-container class="pa-0 ma-0" style="height: 100%; position: relative" align="center">

        <v-row class="ma-0 text-center justify-center" style="width: 100%; margin-bottom: 16px!important">

            <v-img
                    class="header"
                    v-if="headerImage"
                    :src="headerImage"
                    height="150px"
                    style="z-index: 2"
            >
                <v-avatar class="centered-avatar">
                    <img
                            style="width: 100.5%; height: 100.5%; position: absolute; top: 0; object-position: center; object-fit: cover;"
                            class="white--text align-end"
                            :aspect-ratio="1"
                            :src="profileImage"
                    >
                </v-avatar>
            </v-img>

            <v-card class="text-center justify-center pa-2"
                    style="width: 95%;  padding-top: 36px!important; margin-top: -16px!important">
                <v-card-title class="text-center justify-center" style="padding-top: 0px!important">
                    @{{username}}
                </v-card-title>

                <v-btn v-if="myUID != uid"
                       dark
                       fab
                       top
                       small
                       right
                       absolute
                       color="primary"
                       @click="openChatWithUser"
                       style="transform: translate(10%, 85%);">
                    <v-icon dark>mdi-message</v-icon>
                </v-btn>
                <div style="margin-bottom: 16px">
                    <span class="ma-2">
                        <v-icon color="yellow darken-3" style="padding-bottom: 2.5px">mdi-star</v-icon>
                        <b> {{avgRating.toFixed(2)}}/5</b> ({{numRatings}})
                    </span>
                    <span v-if="myRatingIsVisible">&nbsp;&nbsp;<b>|</b>&nbsp;&nbsp;</span>

                    <v-btn v-if="myRatingIsVisible" @click="openRateUserDialog"
                           style="padding-left: 4px; padding-right: 4px; height: 30px!important">
                    <span v-if="myRatingIsVisible && myRating">
                        <v-icon style="padding-bottom: 2.5px" :color="myRatingColor">mdi-star</v-icon>
                        <b>{{myRating}}/5</b> ({{youText}})
                    </span>
                        <span style="font-size: 16px; bold: false" v-if="myRatingIsVisible && !myRating">
                        <v-icon :color="myRatingColor" style="padding-bottom: 2.5px">mdi-star-outline</v-icon>
                        ({{youText}})
                    </span>
                    </v-btn>
                </div>
                <p align="center"><i>"{{description}}"</i></p>

                <p align="center" class="text-with-icon" style="font-size:14px;">
                    <v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-cake-variant
                    </v-icon>
                    {{birthDate}}
                </p>

            </v-card>
        </v-row>

        <v-row class="ma-0 text-center justify-center" style="width: 100%; margin-bottom: 16px!important">
            <v-card class="text-center justify-center pa-2" style="width: 95%;">
                <v-row style="margin-left: 24px">
                    <h4 style="display: inline-block">{{motherLanguagesText}}</h4>
                    <v-spacer></v-spacer>
                </v-row>

                <v-row style="width: 100%; margin-bottom: 10px;  margin-left: 9px">
                    <div class="message received" style="width: 80%; margin-left: 15px">
                        <div v-for="language in knownLanguages" v-bind:key="language" style="display: inline; float: left">
                            <v-avatar>
                                <img
                                        style="width: 65%; height: 65%;  object-fit: cover;"
                                        class="white--text align-end"
                                        :aspect-ratio="1"
                                        :src="createPathToFlagImage(language)"
                                >
                            </v-avatar>
                        </div>
                    </div>
                </v-row>

                <v-row style="margin-right: 24px">
                    <v-spacer></v-spacer>
                    <div>
                        <h4 style="display: inline-block" align="right">{{languagesToPracticeText}}</h4>
                    </div>
                </v-row>

                <v-row style=" width: 100%;">
                    <v-spacer></v-spacer>
                    <div class="message sent" style="width: 80%; margin-right: 15px">
                        <div v-for="language in languagesToLearn" v-bind:key="language"
                             style="display: inline; float: right">
                            <v-avatar>
                                <img
                                        style="width: 65%; height: 65%;  object-fit: cover;"
                                        class="white--text align-end"
                                        :aspect-ratio="1"
                                        :src="createPathToFlagImage(language)"
                                >
                            </v-avatar>
                        </div>
                    </div>
                </v-row>
            </v-card>
        </v-row>

        <v-dialog v-model="rateUserDialog" max-width="290">
            <v-card>
                <v-card-title class="headline">{{textRateUser}}</v-card-title>
                <v-card-text align="center">
                    <v-rating
                            v-model="myProvisionalRating"
                            color="primary"
                            background-color="grey darken-1"
                            empty-icon="$ratingFull">

                    </v-rating>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="removeUserRating">{{textRemove}}</v-btn>
                    <v-btn color="primary" :disabled="myProvisionalRating == null" text @click="setUserRating">
                        {{textRate}}
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>

    import Constants from "../../store/Constants";
    import {mapActions, mapMutations} from 'vuex';
    import AuxMethods from "../../store/AuxMethods";

    export default {
        name: "PublicProfile",
        props: {
            profileImage: null,
            headerImage: null,
            username: null,
            knownLanguages: null,
            languagesToLearn: null,
            birthDate: null,
            description: null,
            avgRating: null,
            numRatings: null,
            myRatingIsVisible: null,
            myRating: null,
            uid: null,
            myUID: null,
            closeText: Constants.TEXT_CLOSE,
        },
        data() {
            return {
                rateUserDialog: false,
                textRateUser: Constants.TEXT_RATE_USER,
                textYou: Constants.TEXT_YOU,
                textRate: Constants.TEXT_RATE,
                textRemove: Constants.TEXT_REMOVE,
                myProvisionalRating: this.myRating,
                snackbar: false,
                snackbarText: null,
                languagesToPracticeText: Constants.TEXT_LANGUAGES_TO_PRACTICE,
                motherLanguagesText: Constants.TEXT_MOTHER_LANGUAGES,
                youText: Constants.TEXT_YOU
            }
        },
        methods: {
            ...mapActions('user', ['rateUser']),
            ...mapMutations('chats', ['addChat']),
            createPathToFlagImage: function (country) {
                return this.$store.getters['user/getLanguageImgByID'](country);
            },
            openRateUserDialog: function () {
                this.myProvisionalRating = this.myRating;
                this.rateUserDialog = true;
            },
            removeUserRating: function () {
                this.myProvisionalRating = null;
                this.setUserRating();
            },
            setUserRating: function () {
                let self = this;
                self.rateUserDialog = false;

                if (self.myRating != self.myProvisionalRating) {

                    let resultRateUser = self.rateUser({
                        'rating': self.myProvisionalRating,
                        'id': self.uid,
                        'prev': self.myRating
                    });


                    resultRateUser.then(() => {
                        self.$emit("update-user-rating", self.myProvisionalRating);
                    })
                        .catch(() => {
                            self.snackbarText = Constants.TEXT_COULD_NOT_SET_YOUR_RATING;
                            self.snackbar = true;
                        });
                }
            },
            openChatWithUser() {
                let self = this;
                if(self.myUID && self.uid){
                    let chatID = AuxMethods.generateChatID(self.myUID, self.uid);
                    self.addChat({
                        users: [self.myUID, self.uid],
                        id: chatID,
                        messages: []
                    });
                    this.$router.push({path: '/dashboard/chats/chat/' + chatID})
                }
                else{
                    self.snackbarText = Constants.ERROR_GENERIC_PROBLEM;
                    self.snackbar = true;
                }

            },

        },
        computed: {
            myRatingColor: function () {
                if (this.myRating) {
                    return '#1976D2';
                } else {
                    return null;
                }
            }
        },

    }
</script>

<style scoped>

    /* Messages */
    .centered-avatar {
        position: absolute;
        top: 120px !important;
        left: 50%;
        height: 18vh !important;
        transform: translate(-50%, -7.5vh);
        width: 18vh !important;
        min-width: 75px;
        min-height: 75px;
        border: 6px solid #fafafa;
        max-height: 200px;
        max-width: 200px;
    }

    /*.right-centered-fab{
        position: absolute;
        top: 90%!important;
        left: calc(50% + 12.5vh);
        padding: 10px;
        transform: translate(-50%, -50%);
    }*/

    .right-fab {
        position: absolute;
        top: 156px;
        right: 0px !important;
    }

    .header {
        margin-bottom: 5vh;
        padding: 0px;
        overflow: visible;
        width: 100%;
        height: 18vh;
    }

    .message {
        color: #000;
        clear: both;
        line-height: 18px;
        font-size: 15px;
        padding: 4px;
        position: relative;
        margin: 4px 0;
        max-width: 85%;
        word-wrap: break-word;
        z-index: 2;
    }

    .message:after {
        position: absolute;
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
    }

    .message:first-child {
        margin: 4px 0 8px;
    }

    .message.received {
        background: lightskyblue;
        border-radius: 0px 5px 5px 5px;
    }

    .message.received:after {
        border-width: 0px 10px 10px 0;
        border-color: transparent lightskyblue transparent transparent;
        top: 0;
        left: -9px;
    }

    .message.sent {
        background: lightskyblue;
        border-radius: 5px 0px 5px 5px;
    }

    .message.sent:after {
        border-width: 0px 0 10px 10px;
        border-color: transparent transparent transparent lightskyblue;
        top: 0;
        right: -9px;
    }
</style>
